export const sendRequest = ( url, method, body = null)=>{
    const headers = {};

    return fetch(url, {
        method,
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        url,
        body
    }).then(response => response.json())
        .catch(err=>  console.log(err))

}